<template>

    <el-container>

        <el-header style="height: 100px">
            <Sms-Header></Sms-Header>
        </el-header>

        <el-container>
            <el-aside width="200px">
                <Sms-Aside></Sms-Aside>
            </el-aside>

            <el-main>
                <transition name="fade" mode="out-in">
                    <router-view></router-view>
                </transition>
            </el-main>
        </el-container>
            <el-footer>
                <Sms-Footer></Sms-Footer>
            </el-footer>

    </el-container>

</template>

<script>
import header from '../../components/header'
import footer from '../../components/footer'
import aside from '../../components/aside'

export default {
  name: 'dashboard',
  components: {
    'Sms-Header': header,
    'Sms-Footer': footer,
    'Sms-Aside': aside
  }
}
</script>

<style scoped>
    .el-header {
        background-image: url('../../components/header/image/banner.png');
        background-size: 100% 100%;
        width: 100%;
        height: 100%;
        margin-top: -16px;
        margin-right: -8px;
        text-align: center;
    }

    .el-main {
        padding: 10px;
    }

    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.4s ease;
    }

    .fade-enter,
    .fade-leave-to {
        opacity: 0;
    }

</style>



