<template>
    <div >
        <el-menu :style="style" router :default-active="activeRoute" background-color="#2d3237" text-color="#fff"
                 active-text-color="#ffd04b">
            <el-menu-item index="/index">
                <i class="el-icon-menu"></i>
                <span slot="title">学生管理</span>
            </el-menu-item>
            <el-menu-item index="/score" >
                <i class="el-icon-menu"></i>
                <span slot="title">成绩管理</span>
            </el-menu-item>
            <el-menu-item index="/course" >
                <i class="el-icon-menu"></i>
                <span slot="title">课程管理</span>
            </el-menu-item>
            <el-menu-item index="/academy" >
                <i class="el-icon-menu"></i>
                <span slot="title">学院管理</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>

<script>
export default {
  name: 'sms-aside',
  data () {
    return {
      style: {
        height: window.innerHeight + 'px'
      }
    }
  },
  computed: {
    activeRoute () {
      return this.$route.path
    }
  }
}
</script>

<style scoped>

</style>



